    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: white;
  padding: 45px 0 0;
}

.headerHambu .listitens {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  list-style: none;
  position: absolute;
  margin-top: -90px;
  width: 23.5vw;
  height: 177.8vh;
  background: linear-gradient(50deg, #344a75d7 0%, #0f0e0ef4 100%);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 2;
  transform: translateX(-20%);
}

#menus.listitens {
  -webkit-animation-name: slide;
          animation-name: slide;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes slide {
  from {
    transform: translateX(100%);
  }
}

@keyframes slide {
  from {
    transform: translateX(100%);
  }
}

.headerHambu .listitens li {
  margin: 2px 8px;
}

.logo {
  margin-left: 50px;
}
.icon {
  position: absolute;
  z-index: 4;
  left: 1%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 44px;
  cursor: pointer;
}

.hamburguer {
  top: 50%;
  left: 10%;
  width: 30px;
  height: 6px;
  background: #ff3300;
  position: absolute;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 0, 5s;
}

.hamburguer::before {
  top: -12px;
  content: "";
  position: absolute;
  width: 37px;
  height: 6px;
  background: #ff3300;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}

.hamburguer::after {
  top: 12px;
  content: "";
  position: absolute;
  width: 37px;
  height: 6px;
  background: #ff3300;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}

.icon.iconActive .hamburguer {
  background: transparent;
  box-shadow: 0 2px 5px transparent;
}

.icon.iconActive .hamburguer::after {
  top: 0;
  background: #fff;
  transform: rotate(225deg);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.icon.iconActive .hamburguer::before {
  top: 0;
  background: #fff;
  transform: rotate(135deg);
  box-shadow: 0 2px 5px rgba(37, 189, 10, 0.885);
}

menumenuOpen {
  width: 30vw;
  height: 200vh;
  background: linear-gradient(50deg, #344a75d7 0%, #0f0e0ef4 100%);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

#menus.menuClose {
  -webkit-animation-name: slide2;
          animation-name: slide2;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes slide2 {
  to {
    transform: translateX(-100%);
  }
}

@keyframes slide2 {
  to {
    transform: translateX(-100%);
  }
}

.menuClose2 {
  display: none;
  width: 0px;
}

